import React from 'react';
import { Scaler } from "dapparatus";
import Ruler from "./Ruler";
import {CopyToClipboard} from "react-copy-to-clipboard";
import Balance from "./Balance";
import i18n from '../i18n';
import axios from 'axios';

function redeemFunds(setBalance){
  let memberNumber = prompt('Enter the phone number of a participating FootHodl member.');
  let amount = prompt('Enter an amount of money to request.');
  if (!memberNumber || !amount){
    return console.error('all values are required');
  }
  axios.get("/request/" + window.foothodl_number + "/" + memberNumber + "/" + amount)
   .then((response)=>{
     let code = prompt('Enter the confirmation code received by the FootHodl member');
     axios.get("/confirm/" + window.foothodl_number + "/" + memberNumber + "/" + code)
      .then((response)=>{
        console.log(response);
        setTimeout(function(){ 
          setBalance(parseFloat(amount).toFixed(2));
        }, 3000);
      });
   });
}

export default ({setBalance, isVendor, buttonStyle,ERC20TOKEN,address, balance, changeAlert, changeView, dollarDisplay, subBalanceDisplay}) => {

  let exchangeButton;
  let redeemButton;

  if(!isVendor){
    exchangeButton  = (
      <button className="btn btn-large w-100" style={buttonStyle.secondary} onClick={()=>{
        changeView('exchange')}
      }>
        <Scaler config={{startZoomAt:400,origin:"50% 50%"}}>
          <i className="fa fa-random"></i> {i18n.t('more_buttons.exchange')}
        </Scaler>
      </button>
    )
  }else{
    exchangeButton  = (
      <button className="btn btn-large w-100" style={buttonStyle.secondary} onClick={()=>{
        changeView('cash_out')}
      }>
        <Scaler config={{startZoomAt:400,origin:"50% 50%"}}>
          <i className="fa fa-credit-card"></i> {"Cash Out"}
        </Scaler>
      </button>
    )
  }
  
  redeemButton  = (
    <button className="btn btn-large w-100" style={buttonStyle.secondary} onClick={()=>{
      redeemFunds(setBalance)}
    }>
      <Scaler config={{startZoomAt:400,origin:"50% 50%"}}>
        <i className="fa fa-gift"></i> {i18n.t('more_buttons.redeem')}
      </Scaler>
    </button>
  )


  return (
    <div className="content bridge row">
      <div className="col-6 p-1">
        <button className="btn btn-large w-100" style={buttonStyle.secondary} onClick={()=>{
          changeView('request_funds')}
        }>
          <Scaler config={{startZoomAt:400,origin:"50% 50%"}}>
            <i className="fas fa-hand-holding-usd"></i> {i18n.t('more_buttons.request')}
          </Scaler>
        </button>
      </div>
      <div className="col-6 p-1">
        {exchangeButton}
      </div>
      <div className="col-12 p-1">
        {redeemButton}
      </div>
    </div>
  )
}
